import React from 'react';
import moment from 'moment';
import { SingleDatePicker } from 'react-dates';
// State is first held in component. Only when user hits submit is it sent to the store.

//const date = new Date(); This is he old way. Use MOMENT!
const now = moment();

export default class ExpenseForm extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      description: props.expense ? props.expense.description : '',
      note: props.expense ? props.expense.note : '',
      amount: props.expense ? (props.expense.amount / 100).toString(): '',
      createdAt: props.expense ? moment(props.expense.createdAt) : moment(),
      calendarFocused: false,
      error: ''
    };
  }

  onDescriptionChange = (e) => {
    const description = e.target.value;
    this.setState(() => ({
      description: description
    }));
  };

  onNoteChange = (e) => {
    const note = e.target.value;
    this.setState(() => ({
      note: note
    }));
  }

  onAmountChange = (e) => {
    const amount = e.target.value;
    if (!amount || amount.match(/^\d{1,}(\.\d{0,2})?$/)) {
      this.setState(()=>({
        amount: amount
      }));
    }
  };

  onDateChange = (createdAt) => {
    if(createdAt){
      this.setState(()=>({
        createdAt: createdAt
      }))
    }
  };

  onFocusChange = ({focused}) => {
    this.setState(() => ({
      calendarFocused: focused
    }));
  };

  onSubmit = (e) => {
    e.preventDefault();

    if(!this.state.description || !this.state.amount){
      this.setState(() => ({
        error: 'Please provide description and amount'
      }));
    } else {
      this.setState(()=>({
        error: ''
      }));
      this.props.onSubmit({
        description: this.state.description,
        amount: parseFloat(this.state.amount,10) *100, //parse into number from string with parsefloat, base 10 *100
        createdAt: this.state.createdAt.valueOf(),//change from moment object to timestamp with valueOf()
        note: this.state.note

      });
    }
  };

  render(){
    return(
      <div>
        <form onSubmit={this.onSubmit}>
          {this.state.error && <h2>{this.state.error}</h2>}
          <input
            type='text'
            placeholder='Description'
            autoFocus
            value={this.state.description}
            onChange={this.onDescriptionChange}
          />
          <input
            type='text' // In order to change to two decimals, type must be text instead of number
            placeholder='Amount'
            value={this.state.amount}
            onChange={this.onAmountChange}
          />
          <SingleDatePicker
            date={this.state.createdAt}
            onDateChange={this.onDateChange}
            focused={this.state.calendarFocused}
            onFocusChange={this.onFocusChange}
            numberOfMonths={1}
            isOutsideRange={()=>{ return false}}
          />
          <textarea
            placeholder='Add a note for your expense (optional)'
            value={this.state.note}
            onChange={this.onNoteChange}
          >
          </textarea>
          <button>Add Expense</button>
        </form>
      </div>
    );
  }
}



